

          .forgot-pass{
            font-size: 19px;
            cursor: pointer;
            font-weight: normal;
          }
          .forgot-pass:hover{
            font-size: 20px;
          
            font-weight: bold;
            transition: 0.3s;
          }
          
          .close .fa-xmark{
            font-size: 29px;
            color: #000;
            position: absolute;
            top: 17px;
            right: 22px;
          }
          .close .fa-xmark:hover{
            color: #3467c2;
            font-size: 30px;
            transform: rotate( 180deg);
            transition: .3s;
          }
          .check-email{
            color: #333;
            font-size: 16px;
            margin-top: 22px;
            line-height: 25px;
          }